* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: #eff3f7;
  height: 100%;
}
html,
body,
.container {
  display: flex;
  justify-content: center;
  height: 100vh;
}
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}
