* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 10px;
}

.movie img {
  max-width: 150px;
  width: 100%;
  position: relative;
  top: -30px;
  padding: 0 15px;
}

.movie_title {
  font-weight: 400;
  font-size: 24px;
}
.movie_year {
  margin: 5px 0px;
}

.movie_genres {
  list-style: none;
  display: flex;
}
.movie_genres li {
  margin-right: 5px;
  margin-bottom: 5px;
}
.movie_year,
.movie_genres,
.movie_summary {
  opacity: 0.3;
}

@media screen and (max-width: 1000px) {
  .movies .movie {
    width: 99%;
  }
}
